.KanbanBoard {
    margin: 8px auto;
    padding: 8px;
    max-width: 800px;
    background-color: #f0f0f0;
    border-radius: 6px;
}

select {
    margin: 4px;
    padding: 4px;
    font-size: 16px;
}

.TicketGroup {
    margin-top: 8px;
}

.GroupHeading {
    font-size: 18px;
    margin-bottom: 6px;
}

.TicketCard {
    background-color: white;
    border: 1px solid #ddd;
    border-radius: 4px;
    padding: 8px;
    margin-bottom: 8px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
}

.TicketTitle {
    font-size: 14px;
    margin-bottom: 3px;
}

.TicketDescription {
    font-size: 10px;
    color: #666;
}

.HorizontalGridContainer {
    display: flex;
    flex-wrap: wrap;
    background-color: #f0f0f0;
    gap: 8px;
}

.HorizontalGrid {
    flex: 1;
    padding: 8px;
    border-radius: 4px;
    margin: 8px;
}

.App-header { 
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #333;
    padding: 10px 20px;
}

.DisplayButton {
    font-size: 16px;
    padding: 8px 16px;
    background-color: #fff;
    color: rgb(9, 9, 9);
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.DropdownMenu {
    position: absolute;
    top: 100%;
    left: 16px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    margin-top: 10px;
    padding: 10px;
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 5px;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
    z-index: 2;
}

.DropdownContainer {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 10px;
}

.GroupingDropdown,
.OrderingDropdown {
    margin-bottom: 5px;
}

.TicketCount {
    font-size: 10px;
    margin-left: 4px;
    background-color: #d9d3d3;
    padding: 2px 4px;
    border-radius: 4px;
}
